import React from "react";
import { Link } from "react-router-dom";

const NavBar = () => {
    return (
        <nav className="navbar">
            <h1>My Blogs</h1>
            <div className="link">
                <Link to="/">HOME</Link>
                <Link id="primary-butt" to="/Create">
                    New Blog
                </Link>
            </div>
        </nav>
    );
};

export default NavBar;
