import React from "react";
import BlogList from "./BlogList";
import useFetch from "./useFetch";

const Home = () => {
    const {data: blogs,isPending,error} = useFetch(" http://localhost:8000/blogs");

    return (
        <div>
            {blogs && <BlogList blogs={blogs} />}
            {isPending && "Loading..."}
            {error && <div>{error}</div>}
        </div>
    );
};

export default Home;
